import {useEffect, useState} from 'react'
import Item from './Item'
import {v4 as uuidv4} from 'uuid'

export default function Form() {

    const [dataContent, setDataContent] = useState([]);

    const [stateInput, setStateInput] = useState('');

    const saveData = newTodos => {
        localStorage.setItem("todos", JSON.stringify(newTodos))
    }

    useEffect(() => {
        if(localStorage.getItem("todos")) {
            setDataContent(JSON.parse(localStorage.getItem("todos")));
        }
    }, [])
    

    const linkedInput = e => {
        // console.log(e);
        setStateInput(e)
    }
    // console.log(`StateInput: ${stateInput}`);

    const addTodolist = e => {
        e.preventDefault();

        const newTodos = [...dataContent];

        const newItemTodolist = {};
        newItemTodolist.txt = stateInput;
        newItemTodolist.id = uuidv4();

        newTodos.push(newItemTodolist)
        setDataContent(newTodos)

        setStateInput('')
        
        saveData(newTodos)
    }

    const deleteItem = id => {
        // console.log(id);
        const filteredState = dataContent.filter(item => {
            return item.id !== id;
        })

        setDataContent(filteredState)

        saveData(filteredState)
    }

    return (
        <div className="m-auto px-4 col-12 col-sm-10 col-lg-6">

            <form
            onSubmit={e => addTodolist(e)}     
            className="mb-3">
                <label htmlFor="todo" className="form-label mt-3"></label>
                <input
                value={stateInput} 
                onChange={e => linkedInput(e.target.value)}
                type="text" 
                className="form-control inp-todo" 
                id="todo" />

                <button className="mt-2 btn btn-outline-info d-block btn-add">Ajouter</button>
            </form>

            <h2 className="text-white-50 display-6">Liste des choses Ã  faire </h2>

            <ul className="list-group">
                {dataContent.map((item, index) => {
                    return (
                        <Item
                        txt={item.txt}
                        key={index}
                        id={item.id}
                        delItem={deleteItem}
                        />
                    )
                })}
            </ul>

        </div>
    )
}
