export default function Item(props) {
    return (
        <li className="border d-flex justify-content-between align-items-center p-2 m-2">
            <div className="p-2 item-txt">{props.txt}</div>
            <button 
            onClick={() => props.delItem(props.id)}
            className="btn btn-outline-danger p-2 h-50">Supprimer</button>
        </li>
    )
}
